
.rodape {
  min-height: 100px;
  background: #161616; /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(120deg, #161616, #2B2836); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: #fff;
  text-align: left;
  font-size: 1.5em
}
@media screen and (max-width: 480px) {
.rodape {
    text-align: center;
    font-size: 1em
}
}



